import { encode } from 'js-base64';
export default {
  name: "businessProfileActionWithPreview",
  props: ['info', 'identity'],
  methods: {
    exitPreview() {
      this.$router.push('/account/home');
    },

    editProfile() {
      let companyId = this.info.id;
      let identity = this.identity;
      let str = '';

      if (companyId) {
        let strObj = {
          cid: companyId,
          action: 'edit'
        };
        str = encode(JSON.stringify(strObj));
      } else {
        let strObj = {
          i: identity,
          action: 'add'
        };
        str = encode(JSON.stringify(strObj));
      }

      if (identity == 2) {
        this.$router.push({
          path: '/business/edit/recruiter',
          query: {
            s: str
          }
        });
      }

      if (identity == 3) {
        this.$router.push({
          path: '/business/edit/school',
          query: {
            s: str
          }
        });
      }

      if (identity == 4) {
        this.$router.push({
          path: '/business/edit/other',
          query: {
            s: str
          }
        });
      }

      if (identity == 5) {
        this.$router.push({
          path: '/vendor/edit/vendor',
          query: {
            s: str
          }
        });
      }
    }

  }
};